* {
	margin: 0;
	padding: 0;
}

.no-gutter > [class*='col-'] {
    padding-right:0;
    padding-left:0;
}

.header {
	background: url(../img/bg.jpg) 50%;
	height: 1070px;
	text-align: center;
}

.navigation li {
	padding-right: 20px;
	text-align: right;
	text-transform: uppercase;
	font-family: ProximaNova;
	color:  #ffffff;
	font-size: 24px;
	font-weight: 600;
	line-height: 60.035px;
}

.navbarproperty {
	margin-top: 10px;
}

.navbarproperty a {
	color: #fff;
}

.navbarproperty a:hover {
	color: #9f9acf;
	text-shadow: 1px 1px 2px black, 0 0 1em white;
	transition: 1s;
}

.navbarproperty a:not(:hover) {
  transition: 1s;
}

.fa-bars {
	color: #fff;
	border: transparent;
}

.fa-bars:hover {
	color: #9f9acf;
	text-shadow: 1px 1px 2px black, 0 0 1em white;
	transition: 1s;
}

.fa-bars:not(:hover) {
  transition: 1s;
}

.header h1 {
	font-family: ProximaNova;
	color:  #fefefb;
	font-size: 71.97px;
	font-weight: 700;
	line-height: 60.035px;
	transform: scaleY(1.0035);
	text-transform: uppercase;
	padding-top: 330px;
}

.header p {
	font-family: PT Sans;
	color:  #fefdfd;
	font-size: 29.99px;
	font-style: italic;
	transform: scaleY(1.0035);
	padding-top: 45px;
}

/*about us*/

.aboutUs {
	padding-top: 125px;
	background-color: #ebebeb;
	
}

.aboutImg {
	background: url(../img/aboutus.jpg) no-repeat;
	height: 555px;
	width: 100%;
	text-align: center;
}

.aboutUsText {
	font-family: PT Sans;
	color:  #665f60;
	font-size: 20px;
