#back2Top {
    width: 80px;
    line-height: 80px;
    overflow: hidden;
    z-index: 999;
    display: none;
    cursor: pointer;
    -moz-transform: rotate(270deg);
    -webkit-transform: rotate(270deg);
    -o-transform: rotate(270deg);
    -ms-transform: rotate(270deg);
    transform: rotate(270deg);
    position: fixed;
    bottom: 50px;
    right: 100px;
    background-color: #9f9acf;
    color: #555;
    text-align: center;
    font-size: 30px;
    text-decoration: none;
    opacity: 0.5;
}
#back2Top:hover {
    background-color: #DDF;
    color: #000;
}

#map {
    height: 900px;  
    width: 100%;  
    position:absolute; 
    top: 4540px; 
    left: 0; 
    z-index: 0;
}

#contactform { 
  position: relative; 
